import React, { FC } from 'react';
import { CommonProps } from '@interface';
import './index.less';

const Container: FC<CommonProps> = (props = {}) => {
    const {
        schema: {
            className = '',
            flexDirection = 'column', // row | column
            layoutType = '', // start | end | center
            children = [],
            logInfo: { itemClickType = '' } = {},
        },
        style = {},
        info = {},
        render,
    } = props;

    return (
        <div
            className={`container ${className} ${layoutType} ${flexDirection}`}
            style={style}
            data-itemClickType={itemClickType}
        >
            {Array.isArray(children) && children.length
                ? children.map((item, index) => {
                    return render({ node: item });
                })
                : null}
        </div>
    );
};

export default Container;
